<template>
  <div class="tu">
    <div class="echarts-bg">
      <div id="myChart" :style="{width: '100%', height: '300px'}"></div>
    </div>

    <el-row :gutter="20"> 
      <el-col :span="12">
        <div class="echarts-bg">
          <div id="myChart1" :style="{width: '100%', height: '300px'}"></div>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="echarts-bg">
          <div id="myChart2" :style="{width: '100%', height: '300px'}"></div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<style>
.tu {
  padding: 15px;
  box-sizing: border-box;
}
.sub {
  display: flex;
  margin-top: 10px;
}
.tu .echarts-bg:first-child{
  margin-bottom: 20px;
}
.mid {
  width: 2%;
}
.echarts-bg {
  background: #ffffff;
  box-sizing: border-box;
  padding: 10px;
}
</style>

<script>
export default {
  name: "eCharts",
  data() {
    return {
      msg: "Welcome to Your Vue.js App"
    };
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      var myChart = this.$echarts.init(document.getElementById("myChart"));
      var myChart1 = this.$echarts.init(document.getElementById("myChart1"));
      var myChart2 = this.$echarts.init(document.getElementById("myChart2"));
      // 绘制图表
      myChart.setOption({
        backgroundColor: "#fff",
        title: { text: "下转医院 转诊住院总费用（万元）" },
        tooltip: {
          trigger: "item",
          formatter: "{c}"
        },
        xAxis: {
          data: [
            "2018.01",
            "2018.02",
            "2018.03",
            "2018.04",
            "2018.05",
            "2018.06",
            "2018.07",
            "2018.08",
            "2018.09",
            "2018.10",
            "2018.11",
            "2018.12"
          ]
        },
        yAxis: {},
        series: [
          {
            type: "line",
            data: [
              1512,
              1622,
              1801,
              1923,
              1967,
              2098,
              2234,
              2254,
              2231,
              2333,
              2400,
              2422
            ]
          }
        ]
      });

      myChart1.setOption({
        backgroundColor: "#fff",
        title: { text: "上海杨思医院 转诊住院总费用（万元）" },
        tooltip: {
          trigger: "item",
          formatter: "{c}"
        },
        xAxis: {
          data: ["第一季度", "第二季度", "第三季度", "第四季度"]
        },
        yAxis: {},
        series: [
          {
            type: "line",
            data: [1098, 1211, 1400, 1450]
          }
        ]
      });

      myChart2.setOption({
        backgroundColor: "#fff",
        title: { text: "上海安达医院 转诊住院总费用（万元）" },
        tooltip: {
          trigger: "item",
          formatter: "{c}"
        },
        xAxis: {
          data: ["第一季度", "第二季度", "第三季度", "第四季度"]
        },
        yAxis: {},
        series: [
          {
            type: "line",
            data: [1222, 1199, 1209, 1333]
          }
        ]
      });
    }
  }
};
</script>


